import React, { useState } from "react";
import "./Horarios.css";
import icon from "../Imagens/Imagens Site/Curious-amico.png";

function Horarios() {
  const [aluno, setAluno] = useState("");
  const [turma, setTurma] = useState("");
  const [motivo, setMotivo] = useState("");
  const [data, setData] = useState("");
  const [bloco, setBloco] = useState("");
  const [enviado, setEnviado] = useState(false);

  const handleEnviar = () => {
    if (aluno && turma && motivo && data && bloco) {
      // Aqui você pode adicionar a lógica para enviar o formulário
      setEnviado(true);
      limparFormulario();
      alert("Controle de horário enviado com sucesso!");
    } else {
      alert("Por favor, preencha todos os campos!");
    }
  };

  const limparFormulario = () => {
    setAluno("");
    setTurma("");
    setMotivo("");
    setData("");
    setBloco("");
  };

  return (
    <div className="Horarios">
      <h1 id="title">Entrada com atraso &amp; saida antecipada</h1>
      <div className="base-site">
        <img src={icon} id="gif" alt="Ícone" />
        <div className="baseEquipamentos">
          <input
            placeholder="Digite o nome do aluno"
            type="text"
            className="a"
            value={aluno}
            onChange={(e) => setAluno(e.target.value)}
          />
          <input
            placeholder="Digite a turma"
            type="text"
            className="a"
            value={turma}
            onChange={(e) => setTurma(e.target.value)}
          />
          <input
            placeholder="Motivo..."
            type="text"
            className="a"
            value={motivo}
            onChange={(e) => setMotivo(e.target.value)}
          />

          <div className="selecao-anfiteatro">
            <input
              placeholder="Digite a data"
              type="datetime-local"
              id="Calendario"
              value={data}
              onChange={(e) => setData(e.target.value)}
            />
            <select
              placeholder="Bloco"
              id="Bloco"
              value={bloco}
              onChange={(e) => setBloco(e.target.value)}
            >
              <option>Bloco</option>
              <option>1</option>
              <option>2</option>
            </select>
          </div>

          <button id="Enviar" onClick={handleEnviar}>
            Enviar
          </button>
        </div>
      </div>
    </div>
  );
}

export default Horarios;
